.header {
  position: absolute;
  top: 0;
  width: 100%;
  background-color: transparent;
}

.header__content {
  display: flex;
  justify-content: flex-end;
  padding: 40px 34px;
  align-items: center;
  transition: 0.5s;
  position: relative;
}

.header__content:after {
  content: '';
  background-color: #f53e4f;
  position: absolute;
  width: 0;
  height: 100%;
  top: 0;
  right: 0;
  z-index: 2;
  transition: ease-in-out 0.5s;
}

