.banner {
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	padding: 200px 50px;
	@include md {
		padding: 54px 20px 58px; }
	@include xs {
		padding: 99px 20px 131px;
		background-position: right; }
	&__content {
		max-width: 740px;
		margin: 0 auto;
		@include md {
			max-width: 327px; } }
	&__text {
		margin-bottom: 70px;
		font-size: 36px;
		@include bold;
		@include md {
			font-size: 20px; }
		@include xs {
			font-size: 16px;
			margin-bottom: 20px; }
		.italic {
			@include italic; } }
	&__image {
		width: 100%;
		@include xs {
			transform: scale(1.2); }
		img {
			width: 100%;
			height: auto; }
		#animation_container3 {
			width: 100%!important;
			display: flex;
			justify-content: flex-start;
			position: relative;
			padding-top: 50%;
			height: 0!important;
			@include xs {
				padding-top: 35%; }
			canvas {
				max-width: 1100px;
				position: absolute;
				width: 100%!important;
				height: auto!important;
				top: 0;
				left: 0; } } } }
