body {
	background: #FFF;
	@include regular;
	color: #000; }

.out {
	overflow-x: hidden;
	width: 100%;
	position: relative;
	&.is-hidden {
		position: fixed;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		overflow: hidden; } }

a {
	text-decoration: none;
	font-size: inherit;
	color: inherit; }

h1 {
	@include semi;
	font-size: 120px;
	line-height: 0.93;
	text-transform: uppercase;
	@include r(1100) {
		font-size: 80px; }
	@include md {
		font-size: 48px; }
	@include xs {
		font-size: 40px; } }

.line-bg {
	background-image: url('../img/line.png');
	background-repeat: no-repeat;
	background-size: 100% 100%;
	@include coverdiv;
	padding-top: 100vh;
	@include xs {
		z-index: 1;
		background-image: url('../img/line2.png'); } }


.line-container {
	position: relative; }

.show-mob {
	display: none;
	@include xs {
		display: block; } }

.hide-mob {
	display: block;
	@include xs {
		display: none; } }

.parallax-block {
	position: relative;
	z-index: 2;
	top: 0; }
