@include font(FiraSans-Bold, FiraSans-Bold);
@include font(FiraSans-Italic, FiraSans-Italic);
@include font(FiraSans-Regular,FiraSans-Regular);
@include font(FiraSans-SemiBold, FiraSans-SemiBold);

@mixin bold {
	font-family: FiraSans-Bold, Arial, sans-serif; }

@mixin italic {
	font-family: FiraSans-Italic, Arial, sans-serif; }

@mixin regular {
	font-family: FiraSans-Regular, Arial, sans-serif; }

@mixin semi {
	font-family: FiraSans-SemiBold, Arial, sans-serif; }
