.btn {
	display: flex;
	align-items: center;
	justify-content: center;
	@include semi;
	text-transform: uppercase;
	color: #ffffff;
	width: 206px;
	height: 54px;
	border-radius: 8px;
	background-color: #f53e4f;
	@include md {
		width: 124px;
		height: 33px;
		border-radius: 6px; } }

.appstore-btn {
	display: block;
	background-image: url("../img/appstore.png");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	width: 216px;
	height: 64px;
	border-radius: 8px;
	transition: 0.3s;
	@include md {
		width: 130px;
		height: 38px; }
	&:hover {
		opacity: 0.5; }
	&--footer {
		opacity: 0.5;
		&:hover {
			opacity: 0.5; } } }
